body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  background: #ecf0f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipelist{
    display: flex;
    align-items: stretch;
}
header {
    background-color: #2c3e50;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 0 15px;
}

header h2{
    text-align: center;
}

header li{
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
}

header a:hover {
    opacity: 1;
}

header nav{
    display: flex;
}

header nav li {
    margin: 0 15px;

}

header nav li:first-child{
    margin-left: 0;
}

header nav li:last-child {
    margin-right: 0;

}

@media (max-width: 1000px) {
    header{
        padding: 10px 50px;
    }
}

@media (max-width: 700px) {
    header{
        flex-direction: column;
    }
    header h2{
        margin-bottom: 15px;
    }
}
.buttons {
    display: flex;
    border: none;
    cursor: pointer;
    overflow: hidden;
    background-color: silver;
    float: right;
    margin-top: 0px;
    margin-right: 8px;
    
}

.buttons-save {
    display: flex;
    border: none;
    cursor: pointer;
    overflow: hidden;
    background-color: silver;
    float: right;
    margin-top: 10px;
    margin-bottom: 5px;
    margin-right: 100px;

}
.recipe-form {
    border: 3px solid ;
    align-self: flex-end;

}
.close-button {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    font-size: 0.9em;
    color: #777;
    text-shadow: 0 1px 0 #fff;
    float: right;
}

.recipe-form-container {
    display: flex;
    align-items: right;
    justify-content: center;
    margin-top: 10px;
}

.style-instructions{
    margin-top: 5px;
}

#recipe-instructions-input{
    
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  background-color: #f8f8f8;
  resize: none;
}

.recipe-form-line {

    margin-top: 20px;
}
.recipe-form-line-ingredients {

    margin-top: 10px;
}
.recipe-card{
    width: 31%;
    min-width: 240px;
    margin: 1%;
    border: 1px solid rgba(160,160,160,0.2);
    background: whitesmoke;
    border-radius: 0 0 2px 2px;
    box-shadow: 0 2px 2px rgba(0,0,0,0.14) ;
}

.recipe-card-content {
    padding: 16px;
}

.recipe-card-img{
    overflow: hidden;
    height: 25%;
}

.recipe-card img{
    width: 100%;
    max-height: 250px;
}

.recipe-title {
    margin: 0;
}

