    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
} */

.recipe-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

@media (max-width: 580px) {
    .recipe-list {
        display: block;
    }
}
